	module.exports = {"data":{"markdownRemark":{"html":"<p>As we have seen in earlier examples, JSON objects can contain both nested objects and nested arrays. Similar to accessing nested objects, Array bracket notation can be chained to access nested arrays.</p>\n<p>Here is an example of how to access a nested array:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>var ourPets = { \n  \"cats\": [\n    \"Meowzer\",\n    \"Fluffy\",\n    \"Kit-Cat\"\n  ],\n  \"dogs\": [\n    \"Spot\",\n    \"Bowser\",\n    \"Frankie\"\n  ]\n};\nourPets.cats[1]; // \"Fluffy\"\nourPets.dogs[0]; // \"Spot\"</code></pre>\n      </div>","fields":{"slug":"/certificates/accessing-nested-arrays/"},"frontmatter":{"title":"Accessing Nested Arrays"}}},"pathContext":{"slug":"/certificates/accessing-nested-arrays/"}}

